@keyframes scale {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.on-start {
  animation: scale 1s infinite;
}

.timer {
  display: flex;
}

.field {
  border: 1px solid #000;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  margin: 50px;
  padding: 10px;
  display: flex;
}

.value {
  font-size: 30px;
}

.label {
  text-transform: uppercase;
  font-size: 10px;
}

